import { HttpClient, NonNullablePaths } from '@wix/sdk-types';
import {
  DefaultPrivacy,
  GetDefaultPrivacyStatusResponse,
  SetDefaultPrivacyStatusResponse,
  getDefaultPrivacyStatus as universalGetDefaultPrivacyStatus,
  setDefaultPrivacyStatus as universalSetDefaultPrivacyStatus,
} from './members-v1-default-privacy-default-privacy.universal.js';

export const __metadata = { PACKAGE_NAME: '@wix/members' };

export function getDefaultPrivacyStatus(
  httpClient: HttpClient
): GetDefaultPrivacyStatusSignature {
  return () =>
    universalGetDefaultPrivacyStatus(
      // @ts-ignore
      { httpClient }
    );
}

interface GetDefaultPrivacyStatusSignature {
  /**
   * Retrieves the default privacy status of a site.
   */
  (): Promise<GetDefaultPrivacyStatusResponse>;
}

export function setDefaultPrivacyStatus(
  httpClient: HttpClient
): SetDefaultPrivacyStatusSignature {
  return (defaultPrivacy: NonNullablePaths<DefaultPrivacy, `revision`>) =>
    universalSetDefaultPrivacyStatus(
      defaultPrivacy,
      // @ts-ignore
      { httpClient }
    );
}

interface SetDefaultPrivacyStatusSignature {
  /**
   * Sets the default privacy status of a site.
   * @param - Default privacy settings to set.
   */
  (defaultPrivacy: DefaultPrivacy): Promise<SetDefaultPrivacyStatusResponse>;
}

export {
  DefaultPrivacy,
  GetDefaultPrivacyStatusRequest,
  GetDefaultPrivacyStatusResponse,
  Privacy,
  SetDefaultPrivacyStatusRequest,
  SetDefaultPrivacyStatusResponse,
} from './members-v1-default-privacy-default-privacy.universal.js';
